import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Button, Modal, Form, Input, Radio, message } from 'antd';
import Cookies from 'js-cookie';
import './index.less';

import patient from '../../api/patient';
import { userLogin } from '../../action/patient';

interface RegisterFormProps {
  changeLogin: () => void;
  onCancel: () => void;
}

interface LoginFormProps {
  changeLogin: () => void;
  onCancel: () => void;
  onLogin: (userInfo: any) => void;
}

const RegisterForm: React.FC<RegisterFormProps> = ({
  changeLogin,
  onCancel
}) => {
  const [form] = Form.useForm();

  const onFinish = async values => {
    const res: any = await patient.register(values);
    if (!res.errCode) {
      message.success('');
      changeLogin();
    } else {
      message.error(res.errMsg);
    }
  };

  const checkTel = async (rule, value, callback) => {
    const res: any = await patient.checkUserTelUnique({userTel: value});
    if (res.errCode) {
      callback('');
    } else {
      callback();
    }
  }

  const sendCode = async (value) => {
    const res: any = await patient.sendCode({phone: value})
    if (!res.code) {
      message.success('');
    } else {
      message.error(res.errMsg);
    }
  }

  return (
    <Form
      form={form}
      layout="vertical"
      name="RegisterForm"
      onFinish={onFinish}
    >
      <Form.Item
        name="userName"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="userPwd"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input.Password />
      </Form.Item>
      <Form.Item 
        name="userGender" 
        label=''
        rules={[{ required: true, message: '' }]}
      >
        <Radio.Group>
          <Radio value=""></Radio>
          <Radio value=""></Radio>
        </Radio.Group>
      </Form.Item>
      <Form.Item
        name="userTel"
        label=""
        rules={[{
          required: true,
          message: ''
        }, {
          pattern: /^1[3456789]\d{9}$/,
          message: ''
        }, {
          validator: checkTel
        }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="code"
        label={(
          <>
            <span></span>
            <Button type="link" onClick={() => sendCode(form.getFieldValue('userTel'))}></Button>
          </>
        )}
        rules={[{
          required: true,
          message: ''
        }]}
      >
        <Input />
      </Form.Item>
      <Form.Item>
        <div style={{textAlign: 'center'}}>
          <Button type="link" onClick={changeLogin}>
            
          </Button>
        </div>
      </Form.Item>
      <Form.Item>
      <div style={{textAlign: 'center'}}>
        <Button type="primary" htmlType="submit">
          
        </Button>
      </div>
      </Form.Item>
    </Form>
  );
};

const LoginForm: React.FC<LoginFormProps> = ({
  changeLogin,
  onCancel,
  onLogin
}) => {
  const [form] = Form.useForm();

  const onFinish = async values => {
    const res: any = await patient.login(values);
    if (!res.data.errCode) {
      message.success('');
      Cookies.set('patient_token', res.headers.authorization);
      onLogin({
        isLog: true, 
        userMsg: res.data.data
      });
      onCancel();
    } else {
      message.error(res.data.errMsg);
    }
  };

  return (
    <Form
      form={form}
      layout="vertical"
      name="LoginForm"
      onFinish={onFinish}
    >
      <Form.Item
        name="userTel"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="userPwd"
        label=""
        rules={[{ required: true, message: '' }]}
      >
        <Input.Password />
      </Form.Item>
      <Form.Item>
        <div style={{textAlign: 'center'}}>
          <Button type="link" onClick={changeLogin}>
            
          </Button>
        </div>
      </Form.Item>
      <Form.Item>
      <div style={{textAlign: 'center'}}>
        <Button type="primary" htmlType="submit">
          
        </Button>
      </div>
      </Form.Item>
    </Form>
  );
};

const LoginAndReg = (props: any) => {

  const [visible, setVisible] = useState(false);
  const [login, setLogin] = useState(true);

  const onCancel = () => {
    setVisible(false);
    setLogin(true);
  };

  return (
    <div className='login-and-reg'>
      <Button
        type="primary"
        onClick={() => {
          setVisible(true);
        }}
      >
        
      </Button>
      <Modal
        visible={visible}
        title={login ? '' : ''}
        onCancel={onCancel}
        footer={null}
      >
        {
          login ? (
            <LoginForm
              changeLogin={() => setLogin(false)}
              onCancel={onCancel}
              onLogin={props.onLogin}
            />
          ) : (
              <RegisterForm
                changeLogin={() => setLogin(true)}
                onCancel={onCancel}
              />)
        }
      </Modal>
    </div>
  );
}

const mapStateToProps = (state: { patient: any; }) => {
  return {
      patient: state.patient
  }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onLogin: (userInfo: any) => {
          dispatch(userLogin(userInfo))
        },
    }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginAndReg);