import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import { Table, Tag, message, Button, Modal } from 'antd';

import patient from '../../../../api/patient';


const columns = [
  {
    title: '',
    dataIndex: 'doctorTitle',
    key: 'doctorTitle',
  }, {
    title: '',
    dataIndex: 'doctorName',
    key: 'doctorName',
  }, {
    title: '',
    dataIndex: 'doctorPhoto',
    key: 'doctorPhoto',
    render: (text, record, index) => {
      return (
        <img src={record.doctorPhoto} width={150}></img>
      )
    }
  }, {
    title: '',
    dataIndex: 'doctorGender',
    key: 'doctorGender',
  }, {
    title: '',
    dataIndex: 'doctorInfo',
    key: 'doctorInfo',
  }
];

function DoctorList(props: any) {

  const { data, chooseDay } = props;

  const [visible, setVisible] = useState(false);
  const [order, setOrder] = useState<any>({});

  const createRecord = async (data) => {
    const res: any = await patient.createRecord(data);
    if (!res.errCode) {
      setVisible(true);
      setOrder(res.data);
    } else {
      message.error(res.errMsg);
    }
  }

  const pay = async (data) => {
    const res: any = await patient.payRecord(data);
    if (!res.errCode) {
      handleCancel();
      message.success('3s');
      setTimeout(function(){ props.history.push('/Patient/Personal/Order') }, 3000);
    } else {
      message.error(res.errMsg);
    }
  }

  const handleCancel = () => {
    setVisible(false);
  };

  return (
    <div>
      <Table
        style={{ marginTop: '20px' }}
        columns={[
          ...columns,
          {
            title: '',
            key: 'action',
            render: (text, record, index) => (
              <>
                <div>
                  <Tag color={'blue'}>
                    08:00 - 12:00{record.amQuota}
                </Tag>
                  <Button type='link'
                    onClick={() => createRecord({
                      recordDate: chooseDay,
                      recordTime: 0,
                      doctorId: record.doctorId
                    })}
                  >  </Button>
                </div>
                <div>
                  <Tag color={'blue'}>
                    14:00 - 18:00{record.pmQuota}
                </Tag>
                  <Button type='link'
                    onClick={() => createRecord({
                      recordDate: chooseDay,
                      recordTime: 1,
                      doctorId: record.doctorId
                    })}
                  >  </Button>
                </div>
              </>
            ),
          },
        ]}
        dataSource={data}
        bordered={true}
        pagination={false}
      />

      <Modal
        title=""
        visible={visible}
        onCancel={handleCancel}
        footer={null}
      >
        <p>{order.doctorName}</p>
        <p>{`${order.departmentName}(${order.departmentPos})`}</p>
        <p>
          {order.recordDate}
          <span>
            {
              order.recordTime ? ' ' : ' '
            }
          </span>
        </p>
        <p>{order.recordFare} </p>
        <div style={{textAlign: 'center'}}>
          <Button type='primary' onClick={() => pay({recordId: order.recordId})}></Button>
        </div>
      </Modal>
    </div>
  )
}

export default withRouter(DoctorList);