import React from 'react';
import { Card, message, Row, Col, Form, Modal, Input, Button, Rate } from 'antd';
import { EditOutlined, EllipsisOutlined, DeleteOutlined } from '@ant-design/icons';
import './index.less';

import patient from '../../../../api/patient';

class Order extends React.Component<any, any> {
  constructor(props) {
    super(props);
    this.state = {
      order: [],
      visible: false,
      visibleAll: false,
      id: '',
      comment: {},
      data: {}
    }
  }

  componentDidMount() {
    this.getOrder();
  }

  handleCancel = e => {
    this.setState({
      visible: false,
    });
  };

  handleCancelAll = e => {
    this.setState({
      visibleAll: false,
    });
  };

  openAll = async (record) => {
    const res: any = await patient.getComment({ recordId: record.recordId });
    if (!res.errCode) {
      this.setState({
        comment: res.data,
        visibleAll: true,
        id: record.recordId,
        data: record
      })
    } else {
      message.error(res.errMsg);
    }
  }

  getOrder = async () => {
    const res: any = await patient.listRecord({
      size: 10,
      p: 1
    });
    if (!res.errCode) {
      this.setState({
        order: res.data.list
      })
    } else {
      message.error(res.errMsg);
    }
  }

  delOrder = async (data) => {
    const res: any = await patient.delRecord(data);
    if (!res.errCode) {
      message.success('');
      let newData = [...this.state.order];
      newData = newData.filter(item => {
        return item.recordId !== data.recordId
      })
      this.setState({ order: newData });
    } else {
      message.error(res.errMsg);
    }
  }

  onFinish = async values => {
    const res: any = await patient.commentRecord({ ...values, recordId: this.state.id });
    if (!res.errCode) {
      message.success('');
      this.getOrder();
      this.setState({ visible: false });
    } else {
      message.error(res.errMsg);
    }
  };

  render() {
    const { order, data, comment } = this.state;
    return (
      <div className='Order'>
        {
          order.map((item, index) => {
            return (
              <Card
                key={index}
                hoverable
                title={`${item.recordId}`}
                extra={item.recordCreateTime.substring(0,10)}
                actions={[
                  <DeleteOutlined onClick={() => this.delOrder({ recordId: item.recordId })} />,
                  <EditOutlined key="edit" onClick={() => this.setState({ visible: true, id: item.recordId })} />,
                  <EllipsisOutlined key="ellipsis" onClick={() => this.openAll(item)} />,
                ]}
              >
                <Row>
                  <Col span={12}>
                    <p>{item.doctorName}</p>
                  </Col>
                  <Col span={12}>
                    <p>{`${item.departmentName}(${item.departmentPos})`}</p>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <p>
                      {item.recordDate}
                      <span>
                        {
                          item.recordTime ? ' ' : ' '
                        }
                      </span>
                    </p>
                  </Col>
                  <Col span={12}>
                    <p>{item.recordOrder ? item.recordOrder : '-'}</p>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <p>{item.recordFare} </p>
                  </Col>
                  <Col span={12}>
                    <p>{item.recordStatus ? item.recordStatus : '-'}</p>
                  </Col>
                </Row>
              </Card>
            )
          })
        }
        <Modal
          title=""
          visible={this.state.visible}
          onCancel={this.handleCancel}
          footer={null}
        >
          <Form
            layout="vertical"
            name="LoginForm"
            onFinish={this.onFinish}
          >
            <Form.Item
              name="feedbackRating"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Rate />
            </Form.Item>
            <Form.Item
              name="feedbackInfo"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Input.TextArea />
            </Form.Item>
            <Form.Item>
              <div style={{ textAlign: 'center' }}>
                <Button type="primary" htmlType="submit">
                  
                </Button>
              </div>
            </Form.Item>
          </Form>
        </Modal>

        <Modal
          title=""
          visible={this.state.visibleAll}
          onCancel={this.handleCancelAll}
          footer={null}
        >
          <Row>
            <Col span={12}>
              <p>{data.doctorName}</p>
            </Col>
            <Col span={12}>
              <p>{`${data.departmentName}(${data.departmentPos})`}</p>
            </Col>
            <Col span={12}>
              <p>
                {data.recordDate}
                <span>
                  {
                    data.recordTime ? ' ' : ' '
                  }
                </span>
              </p>
            </Col>
            <Col span={12}>
              <p>{data.recordOrder ? data.recordOrder : '-'}</p>
            </Col>
            <Col span={24}>
              <p>{data.medicalRecord ? data.medicalRecord : '-'}</p>
            </Col>
            <Col span={24}>
              <p>{comment.feedbackInfo ? comment.feedbackInfo : '-'}</p>
            </Col>
            <Col span={12}>
              <p>{data.recordFare} </p>
            </Col>
            <Col span={12}>
              <p>{data.recordStatus ? data.recordStatus : '-'}</p>
            </Col>
          </Row>
        </Modal>
      </div>
    );
  }
}

export default Order;