import React from 'react';
import { connect } from 'react-redux';
import {
  Form,
  Input,
  Button,
  message
} from 'antd';
import { Title } from '../../../../component';
import './index.less';
import patient from '../../../../api/patient';
import { updateUser } from '../../../../action/patient';

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 14 },
};

class ChangePwd extends React.Component<any, any> {
  constructor(props) {
    super(props);
    this.state = {}
  }

  onFinish = async values => {
    const res: any = await patient.updateUser(values);
    const { updateUser, userMsg } = this.props;
    if (!res.errCode) {
      updateUser({
        ...userMsg,
        ...values
      })
      message.success('');
    } else {
      message.error(res.errMsg);
    }
  };

  render() {
    return (
      <div className='ChangePwd'>
        <Title text=''></Title>
        <Form
          name="validate_other"
          {...formItemLayout}
          onFinish={this.onFinish}
        >

          <Form.Item
            name='oldPwd'
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            name='newPwd'
            label=""
            rules={[{ required: true, message: '' }]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item>
            <div style={{textAlign: 'center', width: '100%'}}>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </div>
          </Form.Item>
        </Form>
      </div>
    )
  };
}

const mapStateToProps = (state: { patient: any; }) => {
  return {
      patient: state.patient
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
      updateUser: (userInfo: any) => {
        dispatch(updateUser(userInfo))
    },
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ChangePwd);