import React from 'react';
import { Form, Select, Button, message } from 'antd';
import { Title } from '../../../component';
import DoctorList from './DoctorList/DoctorList';

import patient from '../../../api/patient';

import './index.less';

const { Option } = Select;

class Register extends React.Component<any, any> {
  constructor(props) {
    super(props);
    this.state = {
      dep: [],
      doc: [],
      workDay: [{
        day: '2020-06-06',
        mean: ''
      }, {
        day: '2020-06-07',
        mean: ''
      }, {
        day: '2020-06-08',
        mean: ''
      }, {
        day: '2020-06-09',
        mean: ''
      }, {
        day: '2020-06-10',
        mean: ''
      }, {
        day: '2020-06-11',
        mean: ''
      }, {
        day: '2020-06-12',
        mean: ''
      }],
      chooseDay: ''
    }
  }

  componentDidMount() {
    this.getDep();
  }

  getDep = async () => {
    const res: any = await patient.getDepartment({});
    if (!res.errCode) {
      this.setState({dep: res.data});
    } else {
      message.error(res.errMsg);
    }
  }

  onFinish = async values => {
    const res: any = await patient.choseDoctor(values);
    if (!res.errCode) {
      this.setState({
        doc: res.data,
        chooseDay: values.date
      });
    } else {
      message.error(res.errMsg);
    }
  };

  render() {
    const { workDay, dep, doc, chooseDay } = this.state;
    return (
      <div className='Register'>
        <Title text=''></Title>
        <Form 
          name="RegisterForm" 
          layout="inline"
          onFinish={this.onFinish}
        >
          <Form.Item
            name="departmentName"
            label=''
            rules={[{ required: true, message: '' }]}
            style={{width: '40%'}}
          >
            <Select placeholder="">
              {
                dep.map((item, index) => {
                  return (
                    <Option key={index} value={item.departmentName}>{item.departmentName}</Option>
                  )
                })
              }
            </Select>
          </Form.Item>
          <Form.Item
            name="date"
            label=''
            rules={[{ required: true, message: '' }]}
            style={{width: '40%'}}
          >
            <Select placeholder="">
              {
                workDay.map((item, index) => {
                  return (
                    <Option key={index} value={item.day}>{item.mean}</Option>
                  )
                })
              }
            </Select>
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
            >
              
            </Button>
          </Form.Item>
        </Form>
        <DoctorList data={doc} chooseDay={chooseDay}></DoctorList>
      </div>
    );
  }
}

export default Register;